import { Component, OnInit } from '@angular/core';
import { DomSanitizer, SafeUrl } from '@angular/platform-browser';
import { ListVideo, Item } from '../interfaces/list-video';
import { ListVideoService } from '../services/list-video.service';

@Component({
  selector: 'app-featured',
  templateUrl: './featured.component.html',
  styleUrls: ['./featured.component.scss']
})

export class FeaturedComponent implements OnInit {
  
  origin: string = 'https://ang4-projects-felipe007.c9users.io/featured';
  
  listResults: ListVideo;
  featuredVideo: Item;
  srcYoutube: SafeUrl;

  constructor(private ListVideoService: ListVideoService,
              private sanitizer: DomSanitizer) { }

  ngOnInit() {
    this.listVideo();
  }

  listVideo = () => {
    this.ListVideoService.listVideo().subscribe( (response) => {
      let videoId = '';
      let preURL = '';
      this.listResults = response;
      console.log(this.listResults);
      this.featuredVideo = this.listResults.items[0];
      videoId = this.featuredVideo.contentDetails.videoId;
      preURL = 'https://www.youtube.com/embed/'+videoId
                        +'?autoplay=1&origin='+this.origin;
      this.srcYoutube = this.sanitizer.bypassSecurityTrustResourceUrl(preURL);
                        
      console.log(this.featuredVideo);
      //console.log("url yt: "+this.srcYoutube);
    }, (error) => {
      alert("Error: " + error.statusText);
    })
  }
  
  onSelect(item: Item){
    let videoId = '';
    let preURL = '';
    
    this.featuredVideo = item;
    videoId = this.featuredVideo.contentDetails.videoId;
    preURL = 'https://www.youtube.com/embed/'+videoId
                      +'?autoplay=1&origin='+this.origin;
    this.srcYoutube = this.sanitizer.bypassSecurityTrustResourceUrl(preURL);
  }

}
