import { Injectable } from '@angular/core';
import { ListVideo } from '../interfaces/list-video';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { catchError, retry } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class ListVideoService {

  //cachedValue: string;
  baseUrl: string = 'https://www.googleapis.com/youtube/v3/playlistItems?part=id%2Csnippet%2CcontentDetails%2Cstatus&maxResults=12&playlistId=UUkJ8uPxz6UvoI6ihgQJZnww&fields=items(contentDetails%2FvideoId%2Cid%2Csnippet(publishedAt%2CchannelId%2CchannelTitle%2Cdescription%2CplaylistId%2Cthumbnails%2Fdefault%2Ctitle))&key=AIzaSyDRtPUX4hJm-97dpJ-aiazRQEFguNq0Q_U'

  constructor(private http: HttpClient) { }
  
  listVideo : Function = () : Observable<ListVideo> => {
    return this.http.get<ListVideo>(this.baseUrl);
  }
  
}
