import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { DomSanitizer, SafeUrl } from '@angular/platform-browser';

export interface DialogData {
  videoId: string,
  description: string,
  title: string,
  publishedAt: string;
}

@Component({
  selector: 'app-video-modal',
  templateUrl: './video-modal.component.html',
  styleUrls: ['./video-modal.component.scss']
})
export class VideoModalComponent implements OnInit {

  origin: string = 'https://ang4-projects-felipe007.c9users.io/featured';
  srcYoutube: SafeUrl;

  constructor(@Inject(MAT_DIALOG_DATA) public data: DialogData,
              private sanitizer: DomSanitizer) { }

  ngOnInit() {
    
    console.log(this.data);
    
    let preURL = 'https://www.youtube.com/embed/'+this.data.videoId
                      +'?autoplay=1&origin='+this.origin;
    this.srcYoutube = this.sanitizer.bypassSecurityTrustResourceUrl(preURL);
    
  }

  

}
