import { Injectable } from '@angular/core';
import { Search } from '../interfaces/search';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { catchError, retry } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class SearchService {

  query: string;
  baseUrl: string= 'https://www.googleapis.com/youtube/v3/search?key=AIzaSyDRtPUX4hJm-97dpJ-aiazRQEFguNq0Q_U&channelId=UCkJ8uPxz6UvoI6ihgQJZnww&part=id%2Csnippet&maxResults=10&order=date&type=video&q=';

  constructor(private http: HttpClient) { }
  
  searchVideos : Function = (query: string) : Observable<Search> => {
    return this.http.get<Search>(this.baseUrl + query);
  }
}
