import { Component, OnInit } from '@angular/core';
import { DomSanitizer, SafeUrl } from '@angular/platform-browser';
import { Search, Item } from '../interfaces/search';
import { SearchService } from '../services/search.service';
import { ActivatedRoute, ParamMap, Router } from '@angular/router';
import { MatDialog, MatDialogRef } from '@angular/material';
import { VideoModalComponent } from '../video-modal/video-modal.component';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.scss']
})
export class SearchComponent implements OnInit {

  searchResults: Search;
  searchQuery: string;
  displayQuery: string;
  
  title: string;

  constructor(private SearchService: SearchService, 
              private route: ActivatedRoute,
              private router: Router,
              public dialog: MatDialog) { }

  ngOnInit() {
    //this.searchQuery = 'brasil';
    //this.searchVideos();
    
    this.route.paramMap.subscribe( (params: ParamMap) => {
      this.searchQuery = params.get('query');
      this.displayQuery = params.get('query');
      this.searchVideos();
    });
    
    this.route.data.subscribe( (result) => {
      this.title = result.title;
      console.log("title query: "+this.title);
    });
  }
  
  searchVideos = () => {
    this.SearchService.searchVideos(this.searchQuery).subscribe( (response) => {
      this.searchResults = response;
      console.log(this.searchResults);
    }, (error) => {
      alert("Error: " + error.statusText);
    })
  }
  
  // Modal
  openLoginForm = (item: Item) => {
    let videoId = item.id.videoId;
    let description = item.snippet.description;
    let title = item.snippet.title;
    let publishedAt = item.snippet.publishedAt;
    this.dialog.open(VideoModalComponent, 
                    {width: '550px', 
                    height: '450px', 
                    data: {videoId: videoId,
                           description: description,
                           title: title,
                           publishedAt: publishedAt}
                    });
  }

}
