import { Component, OnInit } from '@angular/core';
import { ListVideo, Item } from '../interfaces/list-video';
import { ListVideoService } from '../services/list-video.service';

@Component({
  selector: 'app-all-videos',
  templateUrl: './all-videos.component.html',
  styleUrls: ['./all-videos.component.scss']
})
export class AllVideosComponent implements OnInit {

  listResults: ListVideo;

  constructor(private ListVideoService: ListVideoService) { }

  ngOnInit() {
    this.listVideo();
  }
  
  listVideo = () => {
    this.ListVideoService.listVideo().subscribe( (response) => {
      this.listResults = response;
      console.log(this.listResults);
    }, (error) => {
      alert("Error: " + error.statusText);
    })
  }

}
